@import './_search';
@import './reset';
@import url('https://fonts.googleapis.com/css2?family=Chivo:wght@300;400;700&display=swap');
*, *:before, *:after {
    box-sizing: border-box; }

body {
  margin: 0;
  background-color: white; }

.loadContainer {
  font-size: 50px;
  height: 30vh;
  display: flex;
  align-items: center; }

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Chivo', sans-serif;
  color: black;
  font-weight: 300; }

.main {
  display: grid;
  grid-template: {
    areas: "mainFore mainFore mainFore ""week week week";
    rows: 3fr 1fr; }
  grid-gap: 1em;
  height: 88vh;
  width: 64vw;
  animation: expand 500ms ease-in-out; }

.forecastItem {
  border-radius: 20px;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2); }

.MainForecast {
  grid-area: mainFore;
  display: grid;
  grid-gap: 1em;
  grid-template-areas: "Mtemp";
  grid-template-columns: 1fr; }



.mainImgContainer {
  font-size: 2vw;
  padding: 15px;
  // background-color: lightgreen
  grid-area: Mimage;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  img {
    max-width: 80%;
    max-height: 80%; }
  h1 {
    font-size: 3vw;
    font-weight: 500; } }



.MainTemp {
  background-color: #4d7df6;
  color: white;
  border-radius: 20px;
  font-size: 3vw;
  grid-area: Mtemp;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  h1 {
    font-size: 10vw; } }
.Sunrise {
  grid-area: sunrise; }
.Humidity {
  grid-area: humid;
  display: none; }
.Wind {
  grid-area: wind; }
.MoonPhase {
  grid-area: moon; }
.WeeklyForecast {
  grid-area: week;
  display: grid;
  overflow: scroll;
  grid-gap: .5em;
  grid-template-columns: repeat(8, 1fr);
  padding: 10px;
  .imgContainer {
    display: flex;
    align-item: center;
    justify-content: center;
    img {
      height: 40px;
      width: 40px; } }

  .weekItem {
    border: 1px solid lightgray {
      radius: 30px; }
    min-width: 100px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around; } }



.arrow {
  font-size: 100px;
  animation: arrow 1s linear; }


@keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }
@keyframes arrow {
  0% {
    transform: rotate(0deg); }
  100% {} }
@keyframes expand {
  0% {
    transform: translateY(100vw);
    opacity: 0; }
  50% {
    opacity: 1; }

  100% {
    transform: translateY(0); } }



@media screen and ( max-width: 1000px ) {
  .main {
    width: 95vw;
    grid-gap: .5em; }

  .WeeklyForecast {
    grid-gap: .5em; }
  .search {
    width: 90vw;
    input {
      width: 80%;
      font-size: 15pt; }
    .searchSubmit {
      background-color: transparent;
      border: none;
      font-size: 10pt; } }
  .MainTemp {
    font-size: 20pt;
    h1 {
      font-size: 15vw; } } }
