@import url(https://fonts.googleapis.com/css2?family=Chivo:wght@300;400;700&display=swap);
.search{height:8vh;width:60vw;display:flex;align-items:flex-end;justify-content:flex-start;margin-bottom:2vh}.search input{background-color:transparent;border:none;font-size:24pt;font-family:'Chivo', sans-serif}.search input:focus{outline:none}.search .searchSubmit{background-color:transparent;border:none;font-size:16pt}*,*:before,*:after{box-sizing:border-box}html,body,div,span,object,iframe,figure,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,code,em,img,small,strike,strong,sub,sup,tt,b,u,i,ol,ul,li,fieldset,form,label,table,caption,tbody,tfoot,thead,tr,th,td,main,canvas,embed,footer,header,nav,section,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline;text-rendering:optimizeLegibility;-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:none;-moz-text-size-adjust:none;text-size-adjust:none}footer,header,nav,section,main{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}input{-webkit-appearance:none;border-radius:0}*,*:before,*:after{box-sizing:border-box}body{margin:0;background-color:white}.loadContainer{font-size:50px;height:30vh;display:flex;align-items:center}.App{display:flex;flex-direction:column;align-items:center;font-family:'Chivo', sans-serif;color:black;font-weight:300}.main{display:grid;grid-template-areas:"mainFore mainFore mainFore " "week week week";grid-template-rows:3fr 1fr;grid-gap:1em;height:88vh;width:64vw;animation:expand 500ms ease-in-out}.forecastItem{border-radius:20px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2)}.MainForecast{grid-area:mainFore;display:grid;grid-gap:1em;grid-template-areas:"Mtemp";grid-template-columns:1fr}.mainImgContainer{font-size:2vw;padding:15px;grid-area:Mimage;display:flex;flex-direction:column;align-items:center;justify-content:space-around}.mainImgContainer img{max-width:80%;max-height:80%}.mainImgContainer h1{font-size:3vw;font-weight:500}.MainTemp{background-color:#4d7df6;color:white;border-radius:20px;font-size:3vw;grid-area:Mtemp;display:flex;flex-direction:column;align-items:center;justify-content:center}.MainTemp h1{font-size:10vw}.Sunrise{grid-area:sunrise}.Humidity{grid-area:humid;display:none}.Wind{grid-area:wind}.MoonPhase{grid-area:moon}.WeeklyForecast{grid-area:week;display:grid;overflow:scroll;grid-gap:.5em;grid-template-columns:repeat(8, 1fr);padding:10px}.WeeklyForecast .imgContainer{display:flex;align-item:center;justify-content:center}.WeeklyForecast .imgContainer img{height:40px;width:40px}.WeeklyForecast .weekItem{border:1px solid lightgray;border-radius:30px;min-width:100px;display:flex;flex-direction:column;align-items:center;justify-content:space-around}.arrow{font-size:100px;animation:arrow 1s linear}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes arrow{0%{transform:rotate(0deg)}100%{}}@keyframes expand{0%{transform:translateY(100vw);opacity:0}50%{opacity:1}100%{transform:translateY(0)}}@media screen and (max-width: 1000px){.main{width:95vw;grid-gap:.5em}.WeeklyForecast{grid-gap:.5em}.search{width:90vw}.search input{width:80%;font-size:15pt}.search .searchSubmit{background-color:transparent;border:none;font-size:10pt}.MainTemp{font-size:20pt}.MainTemp h1{font-size:15vw}}

